<template>
  <el-container>
    <el-header> {{ this.$route.name }} </el-header>
    <el-main>
      <VueMarkdownIt :source="source" />
    </el-main>
  </el-container>
</template>

<script>
import VueMarkdownIt from "vue3-markdown-it";
// import {ref} from 'vue'

// const count = ref('')

export default {
  components: {
    VueMarkdownIt,
  },
  props: {
    title: {
      type: String,
      default: "",
    },
    url: {
      type: String,
      default: "",
    },
  },
  data() {
    return {
      source: "",
    };
  },
  methods: {
    fetchDate() {
      //  axios
      this.axios.get(this.$route.name + ".md").then((response) => {
        this.source = response.data;
      });
    },
  },
  created() {
    // console.log(this.$route.name)
    this.fetchDate();
  },
  watch: {
    // 

    $route: "fetchDate",
  },
};
</script>

